export default {
	//
	print_goodDet(item){
		//console.log(item)
		let orderInfo;
		let good = uni.getStorageSync("now_product")
		good.objectId = item.good_id
		good.stocks = item
		good.productCode = item.productCode
		
		orderInfo = '<CB></CB><BR>';
		orderInfo += '--------------------------------<BR>';
		orderInfo += ' ' + good.goodsName + '<BR>';
		if (good.stocks) orderInfo += ' ' + good.stocks.stock_name + '<BR>';
		
		if (good.position) orderInfo += ' ' + good.position + '<BR>';
		if (good.producttime) orderInfo += ' ' + good.producttime + '<BR>';
		if (good.nousetime) orderInfo += ' ' + good.nousetime + '<BR>';
		orderInfo += ' ' + good.reserve + '<BR>';
		orderInfo += ':' + good.packageContent +'*' +good.packingUnit + '<BR>';
		orderInfo += ':      ' + good.costPrice + '<BR>';
		orderInfo += ':      ' + good.retailPrice + '<BR>';
		if(good.bad_num) orderInfo += ':      ' + good.bad_num + '<BR>';
		orderInfo += '--------------------------------<BR>';
		orderInfo += '<BR>';
		orderInfo += '<QR>' + good.productCode + '</QR>'; //
					
		this.print_by_code(orderInfo);
	},
	
	//
	print_operations(detail,products){
		console.log(products)
					
		if (detail.type == 3) {
		  orderInfo = '<CB></CB><BR>';
		  orderInfo += '--------------------------------<BR>';
		  for (let i = 0; i < products.length; i++) {
		    orderInfo += '' + products[i].goodsName + '<BR>';
		    orderInfo += '' + products[i].reserve + '<BR>';
		    orderInfo += '' + products[i].now_reserve + '<BR>';
		    orderInfo += '--------------------------------<BR>';
		  }
		}else if (detail.type == -2) {
		  orderInfo = '<CB></CB><BR>';
		  orderInfo += '--------------------------------<BR>';
		  for (let i = 0; i < products.length; i++) {
		    orderInfo += '' + products[i].goodsName + '<BR>';
			orderInfo += '' + products[i].num + '<BR>';
		    orderInfo += '--------------------------------<BR>';
		    for(let item of detail.detail){
				orderInfo += '' + item.stock + '<BR>';
				orderInfo += '<RIGHT>' + (item.reserve-item.num) + '</RIGHT><BR>';
				orderInfo += '' + item.out_stock + '<BR>';
				orderInfo += '<RIGHT>' + (item.out_reserve+item.num) + '</RIGHT><BR>';
			}
		  }
		  orderInfo += '--------------------------------<BR>';
		}  else if (detail.type == 2) {
		  orderInfo = '<CB></CB><BR>';
		  orderInfo += '--------------------------------<BR>';
		  for (let i = 0; i < products.length; i++) {
		    orderInfo += '' + products[i].goodsName + '<BR>';
		    orderInfo += '' + products[i].retailPrice + '<BR>';
		    orderInfo += '    X' + products[i].num + '<BR>';
		    orderInfo += '<RIGHT>' + products[i].total_money + '</RIGHT>';
		    orderInfo += '--------------------------------<BR>';
		    orderInfo += '<BR>';
		    if (detail.custom != null) {
		      orderInfo += '' + detail.custom.custom_name + '<BR>';
		    } else {
		      orderInfo += ' <BR>';
		    }
		  }
		  orderInfo += '<BOLD><RIGHT>' + detail.all_money + '</RIGHT></BOLD>';
		  orderInfo += '--------------------------------<BR>';
		} else {
		  var orderInfo;
		  if (detail.type == 1) {
		    orderInfo = '<CB></CB><BR>';
		  } else {
		    orderInfo = '<CB></CB><BR>';
		  }
		  orderInfo += '--------------------------------<BR>';
		  for (let i = 0; i < products.length; i++) {
		    orderInfo += '' + products[i].goodsName + '<BR>';
		    orderInfo += '' + products[i].retailPrice + '<BR>';
		    orderInfo += '    X' + products[i].num + '<BR>';
		    orderInfo += '<RIGHT>' + products[i].total_money + '</RIGHT>';
		    orderInfo += '--------------------------------<BR>';
		  }
		  orderInfo += '<BOLD><RIGHT>' + detail.all_money + '</RIGHT></BOLD>';
		  orderInfo += '--------------------------------<BR>';
		  if (detail.type == 1) {
		    orderInfo += '<BR>';
		    orderInfo += '<BR>';
		    if (detail.producer != null) {
		      orderInfo += '' + detail.producer.producer_name + '<BR>';
		    }
		    if (detail.real_money == null) {
		      orderInfo += ' <BR>';
		    } else {
		      orderInfo += '' + detail.real_money + '<BR>';
		    }
		    if (detail.debt > 0) {
		      orderInfo += '' + detail.debt + '<BR>';
		    }
		    orderInfo += '--------------------------------<BR>';
		  }
					
		  if (detail.type == -1) {
		    orderInfo += '<BR>';
		    orderInfo += '<BR>';
		    if (detail.custom) {
		      orderInfo += '' + detail.custom.custom_name + '<BR>';
		    }
		    if (detail.real_money) {
		      orderInfo += ' <BR>';
		    } else {
		      orderInfo += '' + detail.real_money + '<BR>';
		    }
					
		    if (detail.debt > 0) {
		      orderInfo += '  ' + detail.debt + '<BR>';
		    }
					
		    orderInfo += '--------------------------------<BR>';
		  }
		}
					
		orderInfo += '' + detail.opreater.nickName + '<BR>';
		orderInfo += '' + (detail.beizhu == '') ? '' + '<BR>' : detail.beizhu + '<BR>';
		orderInfo += '' + detail.createdAt + '<BR>';
					
		this.print_by_code(orderInfo);
	},
	
	//
	print_by_code: function(orderInfo) {
		//USERUKEY http://admin.feieyun.com/ 
		var USER = uni.getStorageSync("setting").USER; // http://admin.feieyun.com/ 
		var UKEY = uni.getStorageSync("setting").UKEY; //keyUKEY

		var SN = uni.getStorageSync("setting").number; //,

		//URL
		var HOST = "api.feieyun.cn"; //
		var PATH = "/Api/Open/"; //
		var STIME = new Date().getTime(); //,
		var SIG = hex_sha1(USER + UKEY + STIME); //

		//
		//:
		//"<BR>","<CUT>"(,)
		//"<LOGO>"LOGO(LOGO),"<PLUGIN>"
		//
		//"<CB></CB>","<B></B>","<C></C>",<L></L>
		//<W></W>,"<QR></QR>","<BOLD></BOLD>","<RIGHT></RIGHT>"
		//
		//



		//******
		//{"msg":"ok","ret":0,"data":"123456789_20160823165104_1853029628","serverExecutedTime":6}
		//{"msg":".","ret":,"data":null,"serverExecutedTime":5}
		// console.log(orderInfo);
		//
		print_r(SN, orderInfo, 1);

		var hexcase = 0;
		var chrsz = 8;

		function hex_sha1(s) {
			return binb2hex(core_sha1(AlignSHA1(s)));
		}

		function core_sha1(blockArray) {
			var x = blockArray;
			var w = Array(80);
			var a = 1732584193;
			var b = -271733879;
			var c = -1732584194;
			var d = 271733878;
			var e = -1009589776;
			for (var i = 0; i < x.length; i += 16) {
				var olda = a;
				var oldb = b;
				var oldc = c;
				var oldd = d;
				var olde = e;
				for (var j = 0; j < 80; j++) {
					if (j < 16)
						w[j] = x[i + j];
					else
						w[j] = rol(w[j - 3] ^ w[j - 8] ^ w[j - 14] ^ w[j - 16], 1);
					var t = safe_add(safe_add(rol(a, 5), sha1_ft(j, b, c, d)), safe_add(safe_add(e, w[j]), sha1_kt(j)));
					e = d;
					d = c;
					c = rol(b, 30);
					b = a;
					a = t;
				}
				a = safe_add(a, olda);
				b = safe_add(b, oldb);
				c = safe_add(c, oldc);
				d = safe_add(d, oldd);
				e = safe_add(e, olde);
			}
			return new Array(a, b, c, d, e);
		}

		function sha1_ft(t, b, c, d) {
			if (t < 20)
				return (b & c) | ((~b) & d);
			if (t < 40)
				return b ^ c ^ d;
			if (t < 60)
				return (b & c) | (b & d) | (c & d);
			return b ^ c ^ d;
		}

		function sha1_kt(t) {
			return (t < 20) ? 1518500249 : (t < 40) ? 1859775393 : (t < 60) ? -1894007588 : -899497514;
		}

		function safe_add(x, y) {
			var lsw = (x & 0xFFFF) + (y & 0xFFFF);
			var msw = (x >> 16) + (y >> 16) + (lsw >> 16);
			return (msw << 16) | (lsw & 0xFFFF);
		}

		function rol(num, cnt) {
			return (num << cnt) | (num >>> (32 - cnt));
		}

		function AlignSHA1(str) {
			var nblk = ((str.length + 8) >> 6) + 1,
				blks = new Array(nblk * 16);
			for (var i = 0; i < nblk * 16; i++)
				blks[i] = 0;
			for (i = 0; i < str.length; i++)
				blks[i >> 2] |= str.charCodeAt(i) << (24 - (i & 3) * 8);
			blks[i >> 2] |= 0x80 << (24 - (i & 3) * 8);
			blks[nblk * 16 - 1] = str.length * 8;
			return blks;
		}

		function binb2hex(binarray) {
			var hex_tab = hexcase ? "0123456789ABCDEF" : "0123456789abcdef";
			var str = "";
			for (var i = 0; i < binarray.length * 4; i++) {
				str += hex_tab.charAt((binarray[i >> 2] >> ((3 - i % 4) * 8 + 4)) & 0xF) +
					hex_tab.charAt((binarray[i >> 2] >> ((3 - i % 4) * 8)) & 0xF);
			}
			return str;
		}

		/*
		 *  Open_printMsg
		 */
		function print_r(SN, orderInfo, TIMES) {
			uni.request({
				url: 'https://' + HOST + PATH,
				data: {
					user: USER, //
					stime: STIME, //
					sig: SIG, //

					apiname: "Open_printMsg", //
					sn: SN, //
					content: orderInfo, //
					times: TIMES, //,1
				},
				method: "POST",
				header: {
					"content-type": "application/x-www-form-urlencoded"
				},
				success: function(res) {
					console.log(res.data)
					if (res.data.ret != 0) {
						uni.showToast({
							icon: "none",
							title: res.data.msg,
						})
					} else {
						uni.showToast({
							icon: "none",
							title: "",
						})
					}
				}
			})
		}
	},
}
